@charset "UTF-8";
/*:::::::::: FOUNDATION :::::::::*/
html {
  scroll-behavior: smooth; }

body {
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体", "ヒラギノ角ゴ Pro W3", "メイリオ", sans-serif; }
  @media (min-width: 320px) {
    body {
      font-size: calc(0.8125rem + ((1vw - 4.8px) * 1.0417)); } }
  @media (min-width: 768px) {
    body {
      font-size: 16px; } }
  body *,
  body ::before,
  body ::after {
    box-sizing: border-box;
    list-style: none;
    text-decoration: none; }

h1,
h2,
h3,
h4 {
  color: purple;
  font-family: "Libre Franklin", sans-serif;
  font-weight: 400; }

.caption {
  font-family: a-otf-midashi-mi-ma31-pr6n, serif; }

section {
  max-width: 1080px;
  margin: 0 auto;
  width: 80vw; }

.wrapper {
  margin: 0 auto;
  max-width: 1366px;
  width: 80vw; }

/*:::::::::: DESIGN ::::::::::*/
/*:::::::::: DESIGN ::::::::::*/
/*:::::::::: DESIGN ::::::::::*/
/*:::::::::: background-color ::::::::::*/
.bg-gray {
  background: dimgray; }

.bg-purple {
  background: purple; }

.bg-smoke {
  background-color: whitesmoke; }

/*:::::::::: font-color ::::::::::*/
.font-black {
  color: #000; }

.font-gray {
  color: darkgray; }

.font-purple {
  color: purple; }

.font-white {
  color: white; }

/*:::::::::: font-size ::::::::::*/
.font-x-small {
  font-size: 13px; }

@media (min-width: 320px) {
  .font-small {
    font-size: calc(0.75rem + ((1vw - 4.8px) * 0.6944)); } }

@media (min-width: 768px) {
  .font-small {
    font-size: 14px; } }

.font-large {
  font-size: 24px; }

.font-x-large {
  font-size: 36px; }

/*:::::::::: LAYOUT ::::::::::*/
/*:::::::::: LAYOUT ::::::::::*/
/*:::::::::: header ::::::::::*/
.header {
  margin-top: 30px;
  padding: 30px 0;
  width: 100%; }
  @media screen and (min-width: 768px) {
    .header {
      height: 25vw;
      max-height: 230px;
      padding: 0; } }
  .header-upper {
    display: flex;
    justify-content: space-between; }
    .header-upper__item {
      align-self: center;
      width: 33.33333333%; }
    .header-upper__symbol-logo {
      text-align: center; }
      .header-upper__symbol-logo img {
        max-width: 100%; }
    .header-upper__shop-nav ul {
      display: flex;
      justify-content: flex-end; }
    .header-upper__shop-nav li {
      margin-left: 20px; }
    .header-upper__menu-bar-wrapper {
      display: none; }
    @media screen and (max-width: 767px) {
      .header-upper__shop-nav {
        display: none; }
      .header-upper__menu-bar-wrapper {
        cursor: pointer;
        display: flex;
        justify-content: flex-end; }
      .header-upper__menu-bar {
        border: solid 1px lightgray;
        border-radius: 5px;
        height: 38px;
        line-height: 38px;
        padding: 5px 0;
        text-align: center;
        width: 34px; } }
  .header-lower {
    margin-top: 18px; }
    .header-lower__global-nav {
      display: flex;
      justify-content: center; }
      .header-lower__global-nav li:not(:first-child) {
        margin-left: 10px; }
    .header-lower__shop-nav {
      display: none; }
    @media screen and (max-width: 767px) {
      .header-lower__global-nav {
        display: none; }
      .header-lower__global-nav.menu-open {
        align-items: center;
        display: flex;
        flex-direction: column;
        animation: open-menu 1s linear forwards; }
        .header-lower__global-nav.menu-open li {
          background: linear-gradient(rgba(128, 0, 128, 0.1), rgba(128, 0, 128, 0.3));
          height: 30px;
          text-align: center;
          width: 200px; }
          .header-lower__global-nav.menu-open li:hover {
            background: linear-gradient(rgba(128, 0, 128, 0.5), rgba(128, 0, 128, 0.7)); }
            .header-lower__global-nav.menu-open li:hover a {
              color: white; }
          .header-lower__global-nav.menu-open li a {
            display: inline-block;
            height: 100%;
            user-select: none;
            width: 100%; }
        .header-lower__global-nav.menu-open li:first-child {
          margin-bottom: 5px;
          padding: 5px 0; }
        .header-lower__global-nav.menu-open li:not(:first-child) {
          margin: 5px 0;
          padding: 5px 0; }
      .header-lower__shop-nav.menu-open {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 30px 0 10px;
        width: 100%;
        animation: open-menu 1s linear forwards; }
        .header-lower__shop-nav.menu-open li {
          background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.3));
          height: 30px;
          line-height: 30px;
          margin: 5px 0;
          text-align: center;
          width: 200px; }
          .header-lower__shop-nav.menu-open li:hover {
            background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)); }
            .header-lower__shop-nav.menu-open li:hover a {
              color: white; }
          .header-lower__shop-nav.menu-open li a {
            display: inline-block;
            height: 100%;
            user-select: none;
            width: 100%; }
      @keyframes open-menu {
        from {
          opacity: 0; }
        to {
          height: 1; } } }

