/*:::::::::: SWIPER ::::::::::*/
.swiper-slide img {
  width: 100%; }

span.swiper-pagination-bullet {
  background: #fff;
  opacity: 0.6; }
  span.swiper-pagination-bullet-active {
    background: purple; }

/*:::::::::: / SWIPER ::::::::::*/
.content {
  text-align: center; }

.news {
  margin: 40px auto 0; }
  .news__deck {
    display: flex;
    justify-content: center; }
  .news__head {
    align-items: stretch;
    display: flex;
    flex: 0 0 20%;
    justify-content: center; }
  .news__heading {
    align-self: center;
    color: #fff;
    font-size: 1rem;
    font-weight: normal;
    letter-spacing: 2px; }
  .news__body {
    align-items: center;
    display: flex;
    flex: 1 1 0;
    padding: 20px;
    text-align: left;
    width: 840px; }
    .news__body .news__content + .news__content {
      margin-top: 7px; }
    .news__body .news__content time {
      margin-right: 10px; }
    .news__body .news__content p {
      display: inline-block; }
  .news__more {
    display: flex;
    font-size: 13px;
    justify-content: flex-end; }

.about {
  align-items: center;
  border-bottom: 2px solid rgba(128, 0, 128, 0.3);
  display: flex;
  flex-direction: column;
  margin: 40px auto 0;
  padding-bottom: 60px; }
  .about__text {
    margin-top: 30px; }
    .about__text p {
      text-align: left; }
  .about__button {
    align-items: center;
    border: 1px solid purple;
    cursor: pointer;
    display: flex;
    height: 50px;
    justify-content: center;
    margin-top: 40px;
    position: relative;
    transition: 0.4s;
    user-select: none;
    width: 200px; }
    .about__button:hover {
      background: purple;
      color: white; }
    .about__button:active {
      opacity: 0.8;
      position: relative;
      top: 1px;
      box-shadow: -1px -1px 5px gray, 1px 1px 5px gray; }

